





























import Vue from 'vue';

export default Vue.extend({
  name: 'home',
  components: {
  },
  data (): {
    twitterUrl: string
  } {
      return {
        twitterUrl: ''
      }
  },
  mounted () {
    if (window.navigator.language === 'ja') {
      this.twitterUrl = "https://twitter.com/onohiromi_cpp"
    } else {
      this.twitterUrl = "https://twitter.com/onohiromi_h"
    }
  }
});
